import { getQueryForWidget, ocpOnAwsDashboardDefaultFilters, ocpOnAwsDashboardStateKey, } from './ocpOnAwsDashboardCommon';
export const selectOcpOnAwsDashboardState = (state) => state[ocpOnAwsDashboardStateKey];
export const selectWidgets = (state) => selectOcpOnAwsDashboardState(state).widgets;
export const selectWidget = (state, id) => selectWidgets(state)[id];
export const selectCurrentWidgets = (state) => selectOcpOnAwsDashboardState(state).currentWidgets;
export const selectWidgetQueries = (state, id) => {
    const widget = selectWidget(state, id);
    const tabsFilter = Object.assign({}, ocpOnAwsDashboardDefaultFilters);
    if (widget.tabsLimit) {
        tabsFilter.limit = widget.tabsLimit;
    }
    return {
        previous: getQueryForWidget(widget, Object.assign({}, ocpOnAwsDashboardDefaultFilters, { time_scope_value: -2 })),
        current: getQueryForWidget(widget, ocpOnAwsDashboardDefaultFilters),
        tabs: getQueryForWidget(widget, Object.assign({}, tabsFilter, { resolution: 'monthly' })),
    };
};
